.home__container__right__content{
    min-height:50px;
    width:100%;
    /* background-color:red; */
    background-color: white;
    border-radius:10px;
    border:1px solid lightgray;
    padding:10px;
    overflow:auto;

}

.home__container__right__content::-webkit-scrollbar{
    display: none;
}
.suggest__top{
    display:flex;
    justify-content: space-between;
}
.home__container__right__follower{
    min-height:75px;
    width:250px;
    display:flex;
    justify-content: flex-start;
    margin-top:10px;
    padding:10px;
}
.home__container__right__follower>div{
    margin-left: 10px;
}
.follow{
    height:35px;
    width:100px;
    background-color:whitesmoke;
    border:1px solid lightgray;
    border-radius:20px;
    display:flex;
    justify-content: center;
    align-items: center;
    margin-top:5px;


}
.follow:hover{
    cursor: pointer;
    background-color:lightgray;
}