.header__main__container{
    height:60px;
    width:100%;
    display:flex;
    justify-content: space-around;
    align-items: center;
    border-bottom:1px solid lightgrey;
    z-index: 10;
    background-color: white;
    position:fixed;
}
.header__logo__main{
    height:100%;
    min-width:30%;
    display:flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    /* background-color:lightsalmon; */

}
.header__logo__main__img{
    height:40px;
    width:40px;
    display:flex;
    justify-content:center;
    align-items: center;
    /* background-color:gold; */
}
.header__logo__main__img>img{
    height:100%;
    width:100%;
    object-fit: contain;

}
.header__logo__main__search__bar{
    height:100%;
    width:50%;
    display:flex;
    justify-content:flex-start;
    align-items:center;
}
.header__logo__main__search__bar>div{
    display:flex;
    justify-content:flex-start;
    align-items:center;
    height:40px;
    width:100%;
    border-radius: 3px;
    padding:10px;
    background-color:whitesmoke;
    transition:width 700ms;
    margin-left: 10px;


}
/* .header__logo__main__search__bar>div:focus{
    width:100%;
} */
.header__logo__main__search__bar__input{
    height:100%;
    width:100%;
    padding:20px;
    border-radius: 3px;
    outline:none;
    border:none;
    background-color: whitesmoke;
    animation-duration:1s search linear;
    transition:width 700ms;

}
/* .header__logo__main__search__bar__input:focus{
    height:100%;
    width:100%;
    cursor:pointer;
} */
.header__icon__main{
    height:100%;
    width:70%;
    display:flex;
    justify-content:flex-end;
    align-items:center;
    padding:20px;
    font-weight: bold;
    color:gray;

}
.active{
    color:rgb(128, 147, 233);
}
.header__icon__main>div{
    height:55px;
    width:80px;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    font-size:.9rem;
    margin-left: 5px;
    /* background-color:red; */

}
.header__icon__main>div:hover{
    cursor: pointer;
}


@media (max-width:600px) {

    .header__logo__main__search__bar{
        display: none;
}
    .hide{
        display:none;
    }
    .header__icon__main>div{
        padding:5px;
    
    }
    .header__logo__main{
        height:100%;
        width:30%;
        display:flex;
        justify-content: flex-start;
        align-items: center;
        z-index: 10;
        padding:5px;
        /* background-color:lightsalmon; */
    
    }
    .header__icon__main{
        height:100%;
        width:70%;
        display:flex;
        justify-content:flex-end;
        align-items:center;
        padding:5px;
        color:grey;
    
    }
}