.left-sidebar__container{
    min-height:350px;
    width: 100%;
    background-color: white;
    border-radius:10px;
    border:1px solid lightgrey;
}
@media (max-width:650px) {
    .left-sidebar__container{
        /* display:none; */
        min-height: 350px;
        width:100%;

    }
    .back__image{
        width:100%;
        min-height:80px;
        border-radius:10px;
        border-bottom-left-radius: 0px;
        border-bottom-right-radius: 0px;
    }
    .back__image>img{
        height:100%;
        width:100%;
        border-radius:10px;
        border-bottom-left-radius: 0px;
        border-bottom-right-radius: 0px;
        
    
    }
    .left-sidebar__profile__identity{
        height:100%;
        width:100%;
        display:flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        /* padding:10px; */
        text-align:center;
        border-bottom:1px solid lightgrey;
        
    
    }
    
}
.back__image{
    width:100%;
    height:80px;
    border-radius:10px;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
}
.back__image>img{
    height:100%;
    width:100%;
    border-radius:10px;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
    object-fit: cover;

}
.avatar__self{
    position: relative;
    top:-35px;
}
.left-sidebar__profile__identity{
    display:flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    /* padding:10px; */
    text-align:center;
    border-bottom:1px solid lightgrey;
    

}
.margin__bottom{
    margin-bottom:15px;
}
.top__border{
    padding:10px;
    height:50px;
    width:100%;
    border-top:1px solid lightgrey;
}