.login__page__main{
    height: 100vh;
    width:100%;
    background-color:white;
}
.login__page__main__div{
    height:500px;
    width:400px;
    background-color:#eae2b7;
    margin:auto;
    border-radius: 20px;
    border: 1px solid lightgrey;
    display:flex;
    justify-content:center;
    align-items:center;
    position: relative;
    top:50px;
    box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;

}
.login__page__main__div>div{
    height:60px;
    width:350px;
    display: flex;
    justify-content:flex-start;
    align-items:center;
    padding:20px;
    border-radius: 20px;
    border: 1px solid lightgrey;
    background-color:white;

}
.login__page__main__div>div>div{
    color:sandybrown;
    margin-left:20px;
}
.login__page__main__div>div:hover{
    background-color:lightgrey;
    cursor: pointer;
}
.login__page__main__div>div>div>img{
    height:50px;
    width:50px;
    border-radius: 100%;
}