.userAdding__main__container{
    height:100vh;
    width:100%;
    background-color: whitesmoke;
    display:flex;
    justify-content:center;
    align-items:center;
}
.user__field{
    height:350px;
    width:600px;
    background-color:white;
    border:1px solid lightgray;
    border-radius:10px;
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction:column;
}
.information__setup{
    height:100%;
    width:100%;
    background-color:white;
    border:1px solid lightgray;
    border-radius:10px;
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction:column;
}


.information__information__Button{
    height:45px;
    width:100%;
    display:flex;
    justify-content: flex-end;
    align-items:center;
    padding:10px;

}
.information__Button{
    height:100%;
    width: 180px;
    background-color:sandybrown;
    border-radius: 20px;
    border:1px solid lightgray;
    color:white;
    font-weight: bold;
    display:flex;
    justify-content: center;
    padding:20px;
    align-items: center;
}
.information__Button:hover{
    cursor: pointer;
    background-color:saddlebrown;
    color:gray;

}
.user__select__img{
    height:80px;
    width:80px;
    display:flex;
    justify-content:center;
    align-items:center;
    border-radius: 100%;
}
.user__select__img>img{
    height:100%;
    width:100%;
    object-fit:cover;
    border-radius: 100%;

}
.usre__input__container{
    height:45px;
    width:80%;
    display:flex;
    justify-content:center;
    align-items:center;
}
.usre__input__container>input{
    height:100%;
    width:100%;
    border-radius: 20px;
    border:1px solid lightgray;
    outline:0;
    padding:10px;
}
.information__setup>div{
    margin-bottom: 20px;
}